.card_container {
    display: flex;
    justify-content: center;
    position: relative;
    width: 500px;
    height: 10rem;
    transition: all 0.1s linear;
    cursor: pointer;
    color: var(--clr-text-main);
    background: linear-gradient(to right, var(--clr-bg-shade), var(--clr-bg));
}

.card_container:hover .image_container {
    background-color: var(--clr-bg);
    border: 10px solid var(--clr-accent);
    transform: scale(1.2);
}

.card_content {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 80%;
    height: 100%;
    padding: 1rem;
    margin-right: 1rem;
    transition: 0.2s;
}

.image_container {
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    transition: 0.1s;

}

.card_container img {
    width: 150px;
    height: 150px;
    object-fit: cover;
    border-radius: 1rem;
    padding: 1rem;
}


.card_container h1 {
    font-size: 2rem;
    font-weight: 500;
}